* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
.todo-list {
    background-color: white;
    margin: auto;
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid #efefef;
    /*box-shadow:*/
                /*!* The top layer shadow *!*/
            /*0 1px 1px rgba(0,0,0,0.15),*/
                /*!* The second layer *!*/
            /*0 10px 0 -5px #eee,*/
                /*!* The second layer shadow *!*/
            /*0 10px 1px -4px rgba(0,0,0,0.15),*/
                /*!* The third layer *!*/
            /*0 20px 0 -10px #eee,*/
                /*!* The third layer shadow *!*/
            /*0 20px 1px -9px rgba(0,0,0,0.15);*/
    box-shadow:
        /* The top layer shadow */
            0 1px 1px rgba(0, 0, 0, 0.15),
        /* The second layer */
            10px 10px 0 -5px #eee,
        /* The second layer shadow */
            10px 10px 1px -4px rgba(0, 0, 0, 0.15),
        /* The third layer */
            20px 20px 0 -10px #eee,
        /* The third layer shadow */
            20px 20px 1px -9px rgba(0, 0, 0, 0.15);
    padding: 30px;
}

.todo-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px 20px 2px;
    margin-top: 20px;
    width: 70%;
    border-bottom: 1px solid #cecece;
    font-family: Roboto, sans-serif;
    font-weight: 100;
    font-size: 15px;
    color: #333333;
}

.todo-item:hover {
    /*box-shadow: inset 8px 0 5px -4px goldenrod, inset -8px 0 5px -4px goldenrod;*/
    border-bottom: 1px solid dodgerblue;
}

input[type=checkbox] {
    margin-right: 10px;
    font-size: 30px;
}

input[type=checkbox]:focus {
    outline: 0;
}

.uk-input {
    border: none;
}

.new-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px 20px 2px;
    margin-top: 20px;
    width: 70%;
    border-bottom: 1px solid #cecece;
    font-family: Roboto, sans-serif;
    font-weight: 100;
    font-size: 15px;
    color: #333333;
}

input:checked + label {
    -webkit-text-decoration: line-through grey;
            text-decoration: line-through grey;
    color: grey;
}

